import React, { Component } from "react";
import "./ImageContainer.css";

import cat1img from "./img/1.webp";
import cat2img from "./img/2.webp";
import cat3img from "./img/3.webp";
import cat4img from "./img/4.webp";
import cat5img from "./img/5.webp";
class ImageContainer extends Component {
	state = {
		imageHash: Date.now(),
		CatImgTmp: cat1img,
		randomCatIndex: "1",
	};

	render() {
		return (
			<div>
				<div className="Container">
					<img
						alt="Kitty"
						id="CatImage"
						src={this.state.CatImgTmp + "?" + this.state.ImageHash}
					></img>
					<br />
				</div>
				<a
					className="button"
					href="#"
					onClick={() => {
						this.setState({
							CatImgTmp: pickRandomCatImg(),
							imageHash: Date.now(),
						});
					}}
				>
					Random
				</a>
			</div>
		);
	}
}

function pickRandomCatImg() {
	var ReturnCatImg;
	let randomNum;
	var catImg;

	randomNum = Math.floor(Math.random() * 5 + 1);
	switch (randomNum) {
		case 1:
			catImg = cat1img;
			break;
		case 2:
			catImg = cat2img;
			break;
		case 3:
			catImg = cat3img;
			break;
		case 4:
			catImg = cat4img;
			break;
		case 5:
			catImg = cat5img;
			break;

		default:
			catImg = cat1img;
			break;
	}
	ReturnCatImg = catImg;
	return ReturnCatImg;
}

export default ImageContainer;
export { pickRandomCatImg };
